import React from 'react';

export default function TodosItems({todo, onDelete}) {
  return (
    <div className='mx-4 my-4'>
      <h4>{todo.title}</h4>
      <p>{todo.desc}</p>
      <button className="btn btn-sm btn-danger" onClick={()=>{onDelete(todo)}} >Delete</button>
    </div>
  )
}
