import React from 'react';
// import './footer.css';

const Footer = () => {
  let footerStyle = {
    position:"relative",
    top:"2vh",
    width:"100%",
    height:"9%",
  }
  return (
    <footer className="bg-dark text-light py-3" style={footerStyle}>
      <p className="text-center">
        copyright @copy; Mytodoslist.com
      </p>
    </footer>
  )
}

export default Footer
