import React, { useState } from 'react'

const AddTodo = ({addTodo}) => {

    // const [sno,setSno] = useState("");
    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");

    const submit = (e) => {
        e.preventDefault();
        if (!title || !desc) {
            alert("title and description cannot be blank.");
        }
        else{
        addTodo(title,desc);
        setTitle("");
        setDesc("");
        }
    }

    

    return (
        <div className="container my-5">
            <h3>Add todo</h3>
            <form onSubmit={submit}>
                <div className=" mb-3">
                    <label htmlFor="title" className="form-label">Enter title</label>
                    <input type="text" value={title} onChange={(e) => setTitle(e.target.value)} className="form-control" />
                </div>
                <div className="mb-3">
                    <label htmlFor="desc" className="form-label">Enter description</label>
                    <input type="text" value={desc} onChange={(e) => { setDesc(e.target.value) }} className="form-control" />
                </div>
                <button type="submit" className="btn btn-success" >addTodo</button>
            </form>
        </div>
    )
}

export default AddTodo
